import { Button } from 'shadcn/components/ui/button';
import { useToast } from 'shadcn/components/ui/use-toast';

export default function ToastExample() {
  const { toast } = useToast();

  return (
    <Button
      variant="outline"
      onClick={() => {
        toast({
          description: 'Look at me! Im a toast!',
        });
      }}
    >
      Show Toast
    </Button>
  );
}
